import CodeMirror from 'codemirror'
import type {Editor} from 'codemirror'
import type {getYamlRootRuleSignature} from './yaml-rule-util'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
import {type CodeMirrorElement, CodeMirrorLintingHelper} from '../codemirror-linter-util'
import lint from './yaml-linters'

class YamlCodeMirrorLintingHelper extends CodeMirrorLintingHelper {
  getRootRule: getYamlRootRuleSignature
  constructor(_getRootRule: getYamlRootRuleSignature) {
    super()
    this.getRootRule = _getRootRule
  }

  async updateLinting(cm: Editor) {
    const annotations = await lint(cm.getValue(), this.getRootRule)
    cm.operation(() => {
      this.clearState()
      this.createMarks(cm, annotations)
    })
  }
}

export function registerLinterEvents(getRootRule: getYamlRootRuleSignature) {
  const codeMirrorLintingHelper = new YamlCodeMirrorLintingHelper(getRootRule)

  observe('.CodeMirror', {
    add(codeMirror: Element) {
      const anyEditor = codeMirror as CodeMirrorElement
      const editor = anyEditor.CodeMirror

      editor?.on('change', () => {
        clearTimeout(codeMirrorLintingHelper.lintTimeout)
        codeMirrorLintingHelper.lintTimeout = setTimeout(() => codeMirrorLintingHelper.updateLinting(editor), 500)
      })
      CodeMirror?.on(editor.getWrapperElement(), 'mouseover', (event: Event) =>
        codeMirrorLintingHelper.showTooltip(editor, event),
      )
      codeMirrorLintingHelper.updateLinting(editor)
    },
  })
}
