// FIXME This bundle implicitly loads CodeMirror off of the window global. This should
// explicitly import the dependency to ensure it exists on the page. We usually include
// all editors in the same bundle as CodeMirror but this stack-template bundle is separate.
// Maybe dynamic imports solve this.

import type {getYamlRootRuleSignature, suggestionsForExpressionSignature} from './yaml-rule-util'
import type {Editor, MarkerRange, Position, TextMarker} from 'codemirror'
import {type Snippet, autocomplete} from './autocomplete-util'
import {getIndentation, replaceWhitespaces} from './utils'
import CodeMirror from 'codemirror'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
import smartIndentation from './yaml-smart-indentation'
import type {CodeMirrorElement} from '../codemirror-linter-util'

const {Pass} = CodeMirror

function hint(cm: Editor) {
  return codeMirrorAutcomleteHelper.hint(cm)
}
hint.supportsSelection = true

let codeMirrorAutcomleteHelper: CodeMirrorAutocompleteHelper
export function registerAutocompleteEvents(getRootRule: getYamlRootRuleSignature) {
  codeMirrorAutcomleteHelper = new CodeMirrorAutocompleteHelper(getRootRule)

  observe('.CodeMirror', {
    add(codeMirror: Element) {
      const anyEditor = codeMirror as CodeMirrorElement
      const editor = anyEditor.CodeMirror

      // linting events
      editor.setOption('hintOptions', {
        hint,
        completeSingle: false,
      })

      // autocomplete events
      editor.setOption('extraKeys', {
        'Ctrl-Space': 'autocomplete',
        Enter: () => codeMirrorAutcomleteHelper.enter(editor),
        ...(/Macintosh/.test(navigator.userAgent) && {'Alt-Space': 'autocomplete'}),
      })

      editor.addKeyMap({
        Tab: (cm: Editor) => codeMirrorAutcomleteHelper.goToNextPlaceholder(cm, cm.getCursor()),
        Backspace: backspace,
      })

      editor.on('change', (cm: Editor) => {
        if (codeMirrorAutcomleteHelper.lastCursor) {
          const cursor = cm.getCursor()
          if (
            codeMirrorAutcomleteHelper.lastCursor.line === cursor.line &&
            codeMirrorAutcomleteHelper.lastCursor.ch === cursor.ch
          )
            return
          codeMirrorAutcomleteHelper.lastCursor = cursor
        }
      })
    },
  })
}

export class CodeMirrorAutocompleteHelper {
  editorHelp: HTMLElement
  defaultEditorHelp: HTMLElement
  lastCursor: Position
  getRootRule: getYamlRootRuleSignature
  suggestionsForExpression: suggestionsForExpressionSignature

  constructor(getRootRule: getYamlRootRuleSignature) {
    this.editorHelp = document.getElementById('yaml-editor-help')!
    this.defaultEditorHelp = document.getElementById('yaml-editor-help-default')!

    this.getRootRule = getRootRule
    this.suggestionsForExpression = getRootRule({}).suggestionsForExpression!
  }

  async hint(cm: Editor) {
    const cursor = cm.getCursor()
    let textBeforeCursor = cm.getRange({line: 0, ch: 0}, cursor)
    // Remove the selected text to allow autocomplete in placeholders when the placeholder contains a description text.
    // Otherwise the description text would be used to calculate the suggestion
    textBeforeCursor = textBeforeCursor.substring(0, textBeforeCursor.length - cm.getSelection().length)
    const suggestions = await autocomplete(
      cm.getValue(),
      textBeforeCursor,
      this.getRootRule,
      this.suggestionsForExpression,
    )
    if (!suggestions) {
      return {list: [], from: CodeMirror.Pos(cursor.line, cursor.ch), to: CodeMirror.Pos(cursor.line, cursor.ch)}
    }
    const to = CodeMirror.Pos(cursor.line, cursor.ch)

    const list = suggestions.map(suggestion => {
      const {snippet, replace, displayText, description} = suggestion
      const text = snippet.tokens[0]
      const from = replace
        ? CodeMirror.Pos(cursor.line, cursor.ch - replace.length)
        : CodeMirror.Pos(cursor.line, cursor.ch)
      return {
        text,
        displayText,
        from,
        to,
        hint: () => {
          cm.replaceRange('', from, to)
          this.insertSnippet(cm, snippet, true)
        },
        render: (element: Element) => {
          const textSpan = document.createElement('span')
          textSpan.textContent = displayText
          element.appendChild(textSpan)

          if (description) {
            const descriptionSpan = document.createElement('span')
            descriptionSpan.textContent = description
            descriptionSpan.className = 'CodeMirror-hint-description pl-2 text-small color-fg-muted'
            element.appendChild(descriptionSpan)
          }
        },
      }
    })
    const from = list.length > 0 ? list[0]!.from : undefined
    return {list, from}
  }

  showHelp(editor: Editor, help: string) {
    if (!this.editorHelp || !this.defaultEditorHelp) return
    this.lastCursor = editor.getCursor()
    const mark = findNextPlaceholder(editor, editor.getCursor())
    if (!mark && !help) {
      this.defaultEditorHelp.hidden = false
      this.editorHelp.hidden = true
      return
    }

    const placeholder = this.editorHelp.querySelector<HTMLElement>('.js-yaml-editor-help-placeholder')!
    if (help) {
      placeholder.textContent = `${help}.`
      placeholder.hidden = false
    } else {
      placeholder.hidden = true
    }
    this.editorHelp.querySelector<HTMLElement>('.js-yaml-editor-help-tab')!.hidden = !mark
    this.defaultEditorHelp.hidden = true
    this.editorHelp.hidden = false
  }

  goToNextPlaceholder(editor: Editor, from: Position): typeof Pass | undefined {
    const mark = findNextPlaceholder(editor, from)
    if (!mark) {
      editor.showHint()
      return Pass
    }

    const location = mark.find() as MarkerRange
    editor.setSelection(location.from, location.to)
    mark.clear()
    editor.showHint()

    const placeholder = mark.attributes && mark.attributes.placeholder
    this.showHelp(editor, typeof placeholder === 'string' ? placeholder : '')
  }

  insertSnippet(editor: Editor, snippet: Snippet, indent: boolean) {
    const {tokens, help} = snippet

    if (tokens.length > 1) {
      cleanPlaceholders(editor)
    }

    const cursor = editor.getCursor()
    const ind = indent ? getIndentation(editor.getLine(cursor.line)) : ''
    const tab = getIndentationUnit(editor)
    for (let i = 0; i < tokens.length; i += 2) {
      const token = replaceWhitespaces(tokens[i]!, tab, ind)
      const placeholder = tokens[i + 1]

      editor.replaceSelection(token)
      const from = editor.getCursor()
      if (placeholder !== undefined) {
        editor.replaceSelection(replaceWhitespaces(placeholder || ' ', tab, ind)) // marks with empty strings are not visible. We need at least one char
        const to = editor.getCursor()
        editor.markText(from, to, {
          className: 'border color-border-accent-emphasis',
          clearOnEnter: true,
          clearWhenEmpty: true,
          attributes: {placeholder: help ? help[Math.floor(i / 2)]! : ''},
        })
      }
    }

    if (snippet.keepCursorLocation !== true) {
      this.goToNextPlaceholder(editor, cursor)
    }
  }

  enter(editor: Editor): typeof Pass | undefined {
    const cursor = editor.getCursor()
    // If the user hits enter in the middle of a line, or in an empty line, don't do anything
    const currentLine = editor.getLine(cursor.line)
    if (currentLine.substring(cursor.ch).trim().length > 0 || currentLine.trim().length === 0) return Pass

    const textBeforeCursor = editor.getRange({line: 0, ch: 0}, cursor)
    const snippet = smartIndentation(textBeforeCursor, getIndentationUnit(editor), this.getRootRule(textBeforeCursor))
    this.insertSnippet(editor, snippet, false)
  }
}

function isPlaceholderMark(mark: TextMarker) {
  return mark.attributes && 'placeholder' in mark.attributes
}

function getIndentationUnit(editor: Editor) {
  if (editor.getOption('indentWithTabs')) return '\t'
  return ' '.repeat(editor.getOption('indentUnit')!)
}

function cleanPlaceholders(editor: Editor) {
  for (const mark of editor.getAllMarks().filter(isPlaceholderMark)) {
    if (typeof mark.clear === 'function') {
      mark.clear()
    }
  }
}

function findNextPlaceholder(editor: Editor, from: Position) {
  const lastLine = editor.lineCount() - 1
  const end = {line: lastLine, ch: editor.getLine(lastLine).length}
  const marks = editor.findMarks(from, end)
  return marks.find(isPlaceholderMark)
}

function backspace(cm: Editor): typeof Pass | undefined {
  const {line, ch} = cm.getCursor()
  const currentLine = cm.getLine(line)
  const text = currentLine.substring(0, ch)
  if (text.match(/^\s+$/)) {
    const indent = getIndentationUnit(cm)
    if (text.endsWith(indent)) {
      cm.replaceRange('', {line, ch: ch - indent.length}, {line, ch})
      return
    }
  }
  return Pass
}
