import {type RootRule, ruleForPath} from './yaml-rule-util'
import {findNode, getCurrentNode, getSimplifiedText, parse} from './utils'
import type {Snippet} from './autocomplete-util'

export default function smartIndentation(textBeforeCursor: string, ind = '  ', rootRule: RootRule): Snippet {
  const {simplifiedText} = getSimplifiedText(textBeforeCursor)
  const lines = simplifiedText.split('\n').filter(str => str.trim().length > 0)
  const line = lines[lines.length - 1] || ''
  const match = line.match(/^\s*(-\s*)?/)
  let indentation = match ? match[0].replace('-', ' ') : ''

  // beginning of a multiline string
  if (line.match(/\|\s*$/)) {
    return {tokens: [`\n${indentation}\t`]}
  }

  const defaultReturn = {tokens: [`\n${indentation}`]}

  try {
    const {result, lastValue: lastParsedValue, consumeKey} = parse(simplifiedText, '')
    const {path, lastValue: lastDataValue} = getCurrentNode(result)
    const lastValue = lastParsedValue != null ? lastParsedValue : lastDataValue
    if (consumeKey) path.pop()
    // If the current line ends with an inlined array. We need to check if it ends with ]
    // and if the parent node is an array (because could be it's just a string ending with ])
    if (line.match(/]\s*$/) && Array.isArray(findNode(result, path.slice(0, path.length - 1)))) {
      path.pop()
    }

    if (lastValue) {
      // Inside a multiline string. We check that the last value contains multiple lines and
      // that the last line matches the last line in the multiline string (removing the extra empty line the parser adds).
      // We do the last line check to ignore inlined multiline strings (e.g. `foo: "bar\nbaz"`)
      const multilines = lastValue.split('\n')
      if (multilines.length > 1 && line === `${indentation}${multilines[multilines.length - 2]}`) {
        return defaultReturn
      }

      indentation = indentation.replace(ind, '') // Remove one indentation
      path.pop()
    }
    if (path.length === 0) return defaultReturn

    const rule = ruleForPath(path, rootRule)
    if (!rule) return defaultReturn
    if (rule.items) {
      return {tokens: [`\n${indentation}\t- `]}
    }
    if (rule.map) {
      const tokens = [`\n${indentation}\t`, rule.keyPlaceholder || 'key']
      // A map rule may have line-breaking attributes: (rules, map) and non-breaking attributes: (single)
      // Only break the indented line if all possible attributes are line-breaking
      if ((rule.map.rules || rule.map.map) && !rule.map.single) {
        tokens.push(`:\n${indentation}\t\t`)
        tokens.push('')
      } else if (rule.map.items && rule.map.items.single) {
        tokens.push(': [', 'value1', ', ', '...valueN', ']')
      } else {
        tokens.push(': ')
        tokens.push('')
      }

      return {
        tokens,
        help: [rule.keyPlaceholderHelp || ''],
      }
    }
    if (rule.rules) {
      return {tokens: [`\n${indentation}\t`]}
    }
    return defaultReturn
  } catch (err) {
    return defaultReturn
  }
}
