// @ts-nocheck

import {getRootRule} from './workflow-rules'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
import {registerAutocompleteEvents} from '../codemirror-autocomplete-util'
import {registerLinterEvents} from '../yaml-codemirror-linter-util'

registerLinterEvents(getRootRule)
registerAutocompleteEvents(getRootRule)

type CheckReadinessResponse = {
  anyRunnersConfigured: boolean
}

// Returns true if there are self-hosted runners registered or shared with the repository.
async function getRunnerConfigurationRequired(element: HTMLElement): Promise<boolean> {
  const url = element.getAttribute('data-check-readiness-path')!
  const response = await fetch(url, {headers: {Accept: 'application/json'}})
  if (!response.ok) {
    return false
  }

  const data = (await response.json()) as CheckReadinessResponse
  return !data.anyRunnersConfigured
}

observe('.js-workflow-editor-no-runners-registered-container', async (element: HTMLElement) => {
  if (await getRunnerConfigurationRequired(element)) {
    element.hidden = false
  }
})
